@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap');



*{
  padding: 0;
  margin: 0;
  font-family: 'Noto Sans JP', sans-serif;
}


/* hero-sec */
.hero{
  position: relative;
}

.hero-text{
  position: absolute;
  top: 10%;
  left: 40%;
  font-weight: 700;
}

.hero-text h1{
  font-family:"Yu Gothic", "游ゴシック", YuGothic, "游ゴシック体";
  font-size: 1000%;
  font-weight: 700;
}

.hero-text p{
  font-size: 50px;
  display: inline-block;
  padding-left: 200px;
}

.hero-img img{
  width: 100vw;
  height: 100vh;
}






/* title-sec */
.title{
  font-size: 2.5rem;
  padding: 15% 20% 5% 20%;
  text-align: center;
  color: rgb(24, 32, 25);
  font-weight: 700;
}

.title h1{
  border-bottom: solid 10px;
}







/* main-sec */
.main{
  background-color: rgb(237, 250, 250);
  padding-bottom: 5%;
}
 .main-content{
  display: flex;
 }

 .main-content-img{
  padding-left: 10%;
 }

 .main-content-img img{
  height: 120%;
  width: 230%;
 }

 .main-content-text{
  padding-right: 8%;
  padding-left: 25%;
  padding-top: 1%;
 }

 .main-content-text h1{
  font-size: 300%;
  font-weight: 700;
  border-bottom: solid 5px;
 }

 .main-content-text p{
  font-size: 200%;
  padding-top: 8%;
  font-weight: 400;
 }




 /* detail-sec */
 .detail-card{
  display: flex;
 }




/* message-sec */
 .message{
  background-color: rgb(235, 238, 229);
  padding-bottom: 5%;
  margin-top: 2%;
 }

 .message-content{
  display: flex;
  margin: 0 5% 0 10%;
 }

 .message-img h1{
  margin-left: 5%;
  margin-bottom: 5%;
 }

 .message-img img{
  width:80%;
  height: 90%;
  border-radius: 100%;
 }

 .message-text{
  padding-top: 3%;
 }

 .main-text h1{
  font-family:"Yu Gothic", "游ゴシック", YuGothic, "游ゴシック体";
  font-weight: 700;
  font-size: 600%;
 }

 .sub-text p{
  padding-top: 5%;
  font-size: 200%;
 }




/* contact-sec */
.contact-content{
  margin: 0 20%;
}

 .contact-content h1{
  text-align: center;
  font-weight: 700;
  font-size: 500%;
 }

 .contact-content p{
  font-size: 200%;
  padding-top: 5%;
  padding-bottom: 5%;
  text-align: center;
 }


 .last-text{
  padding: 4% 0;
  margin-top: 5%;
  background-color: rgb(230, 227, 233);
  justify-content: center;
  align-items: center;
  text-align: center;
 }

 .last-text h3{
  letter-spacing: 1.2rem;
  font-weight: 700;
  font-size: 300%;
 }

 .last-text a{
  display: inline-block;
  padding-top: 2%;
  font-size: 200%;
  color: black;
  font-weight: 500;
 }